	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Registered Mail","date":"29 Nov 2003","slug":"/2003-11-29-registered-mail/","tags":"Y2003"},"html":"<p>The <a title=\"Deutsche Post\" href=\"http://www.deutschepost.de/dpag?lang=de_EN\">post</a> left a notice that there is a registered letter requiring my signature waiting for me. I can pick it up Monday after 10 am at the stationers, our local post agent. </p>\n<p>I wasn't expecting any such letter, and I have no idea what it could be. It might be for MamaMaus, although the form has clealy 'Frau' and 'Firma' crossed-out, so that the letter is for 'Herrn Hanson-Petersen' (which isn't me, there's no Petersen in my name, but that's beside the poinrt).  The last registered letter we received was for her, a speeding ticket (with fine and points). The US consulate sent my passport via registered mail. A pink slip from my employer might be registered, but the deadline for layoffs has passed for this quarter. Financial documents might also be sent registered.</p>\n<p>Now I am expecting a <a title=\"PapaScott: (Not) Changing Religions\" href=\"http://www.papascott.de/2003/11/23/2712.php\">package</a> to arrive COD, and that would involve the same form being left in our mailbox, except that the 'COD' box would be ticked instead of that for 'Registered Mail'. I assume the post knows how to fill out their own forms. So I have no idea what to expect. It's probably not good news. It's either bad or indifferent. I'll let you know, unless the whole thing is too personal or embarassing. Then I won't.</p>"},"next":{"fields":{"title":"Script for a new Jagermeister commercial?","date":"27 Nov 2003","slug":"/2003-11-27-script-for-a-new-jagermeister-commercial/"}},"previous":{"fields":{"title":"Sweets and flowers","date":"29 Nov 2003","slug":"/2003-11-29-sweets-and-flowers/"}}},{"node":{"fields":{"title":"Script for a new Jagermeister commercial?","date":"27 Nov 2003","slug":"/2003-11-27-script-for-a-new-jagermeister-commercial/","tags":"Y2003"},"html":"<p><a title=\"She pours two shots. She drinks one, and pours the other down her cleavage... \" href=\"http://accordionguy.blogware.com/blog/_archives/2003/11/27/7419.html\">Script for a new Jagermeister commercial?</a></p>"},"next":{"fields":{"title":"Mama Pirate","date":"27 Nov 2003","slug":"/2003-11-27-mama-pirate/"}},"previous":{"fields":{"title":"Registered Mail","date":"29 Nov 2003","slug":"/2003-11-29-registered-mail/"}}},{"node":{"fields":{"title":"Mama Pirate","date":"27 Nov 2003","slug":"/2003-11-27-mama-pirate/","tags":"Y2003"},"html":"<p><img alt=\"mamapirate.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2003/11/mamapirate.jpg\" width=\"320\" height=\"301\" border=\"0\" /></p>\n<p>So this is what MamaMaus was doing in <a href=\"http://www.papascott.de/2003/11/03/2676.php\">Malta</a> a couple of weeks ago. Planning hostile takeovers, eh?</p>"},"next":{"fields":{"title":"Closed on Thanksgiving","date":"27 Nov 2003","slug":"/2003-11-27-closed-on-thanksgiving/"}},"previous":{"fields":{"title":"Script for a new Jagermeister commercial?","date":"27 Nov 2003","slug":"/2003-11-27-script-for-a-new-jagermeister-commercial/"}}}],"pathPrefix":"","first":false,"last":false,"index":580,"pageCount":1101}}

